/*primer parrafo de la pagina de "nosotros"*/

.btn:hover {
    box-shadow: 0 0 5px rgb(0, 255, 72), 0 0 25px, 0 0 50px rgb(0, 255, 72), 0 0 100px rgb(0, 255, 72), 0 0 200px rgb(0, 255, 72);
}
/*Boton de la pagina "prueba"*/
.btn {
    background-color: var(--color-primario);
    border-radius: 5px;
    padding: 20px;
    margin: 10px;
    box-shadow: 0 0 5px rgb(6, 239, 130), 0 0 25px rgb(6, 239, 130);
    cursor: pointer;
  
    &:hover {
      box-shadow: 0 0 5px rgb(0, 255, 72), 0 0 25px rgb(0, 255, 72), 0 0 50px rgb(0, 255, 72), 0 0 100px rgb(0, 255, 72), 0 0 200px rgb(0, 255, 72);
    }
  }
  
  /*Fin del formulario de la pagina "prueba"*/
  /*Boton de la pagina  "tienda"*/
  .boton {
    background: var(--color-gradient1);
    border-radius: 25px;
    padding: 25px;
    margin: 50px 0px;
    box-shadow: 0 0 5px rgb(6, 239, 130) 0 0 25px rgb(6, 239, 130);
  
    &:hover {
      box-shadow: 0 0 5px rgb(0, 255, 72), 0 0 25px, 0 0 50px rgb(0, 255, 72), 0 0 100px rgb(0, 255, 72), 0 0 200px rgb(0, 255, 72);
    }
  }
  
  .btnTienda {
    background: var(--color-negro);
    border-radius: 15px;
    padding: 20px;
    margin: 10px;
    cursor: pointer;
    color: var(--color-blanco);
  
    &:hover {
      box-shadow: 0 0 5px rgb(0, 255, 72), 0 0 25px, 0 0 50px rgb(0, 255, 72), 0 0 100px rgb(0, 255, 72), 0 0 200px rgb(0, 255, 72);
    }
  }
  
  /*Fin de la pagina de tienda*/