/*Vista tablet*/
/*768px hasta 1023px aproximadamente*/

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  #mapa {
    width: 100%;
    height: 500px;
    border: none;
  }
  
  nav ul {
    list-style: none;
    padding: 0;
  
    li {
      display: inline-block;
      margin-right: 20px;
    }
  }
  
  a {
    color: #070707;
    text-decoration: none;
  }
  
  /* Establece un tamaño de fuente más pequeño y otros ajustes para dispositivos móviles */
  @media (min-width: 992px) {
    #BotonHorarios {
      display: none;
    }
  }
  
  @media (max-width: 991px) {
    #tabla {
      display: none;
    }
  }
  
  @media (max-width: 710px) {
    .card {
      width: 100%;
    }
  
    .container {
      justify-content: center;
    }
  }
  